import {Component, Input, OnInit} from '@angular/core';
import {AfricleanObservablesProvider} from '../../../providers/africlean-observables/africlean-observables.provider';
import {AfricleanModalProvider} from '../../../providers/africlean-modal/africlean-modal.provider';
import {AfricleanHttpIncidentProvider} from '../../../providers/africlean-http/africlean-http-incident.provider';
import {ActivatedRoute} from '@angular/router';
import {AfricleanSessionProvider} from '../../../providers/africlean-session/africlean-session.provider';
import {UserRole} from '../../../providers/utils/commonEnum';

@Component({
    selector: 'app-incident',
    templateUrl: './incident.page.html',
    styleUrls: ['./incident.page.scss'],
})
export class IncidentPage implements OnInit {
    /**
     * numéro des pages
     */
    public positions = [];

    /**
     * Position de la page courante
     */
    @Input() public currentPage = 1;

    /**
     * Subscribes temporaires
     */
    public incidentsTmp: any[];
    public incidents: any[];

    /**
     * La limite pour récuperer les données
     */
    public limit = 14;

    /**
     * Données à ignorer
     */
    public skip = 0;

    /**
     * Le nombre de clients par pages
     */
    @Input() public nbrIncidentsPerPage = 7;

    phoneNumber: string;
    public role: number;

    constructor(private observableProvider: AfricleanObservablesProvider,
                private modal: AfricleanModalProvider,
                private incidentProvider: AfricleanHttpIncidentProvider,
                private session: AfricleanSessionProvider,
                private route: ActivatedRoute) {
    }

    ngOnInit() {
        this.getIncidentsByFilter();
        this.phoneNumber = '343 345 564';
        this.observableProvider.loadIncident.subscribe(
            (loadIncident) => {
                if (loadIncident) {
                    this.incidents = [];
                    this.incidentsTmp = [];
                    this.limit = 10;
                    this.skip = 0;
                    this.getIncidentsByFilter();
                }
            });
    }


    sendPosition(position) {
        this.currentPage = position;
        if (position % 2 === 0 && !this.positions.includes(position)) {
            this.positions.push(position);
            this.skip = this.skip + this.limit;
            this.getIncidentsByFilter();
        }
    }

    private async getIncidentsByFilter() {
        const  body = {
          limit: this.limit,
          skip: this.skip
        };
        let userId: number;
        this.role = await this.session.getItem('role');
        if (this.role === UserRole.CLIENT) {
            userId = await this.session.getItem('userId');
            const url = 'incident/user-incident/' + userId;
            this.incidentProvider.save(url, body).subscribe(
                (incidents) => {
                    if (!this.incidents && !this.incidentsTmp) {
                        this.incidents = [];
                        this.incidentsTmp = [];
                        this.positions = [];
                    }
                    this.incidents = this.incidents.concat(incidents.slice());
                    this.incidentsTmp = this.incidentsTmp.concat(this.incidentsTmp.slice());
                }
            );
        }
    }

    /**
     * Retourne tous les incidents liés à un user
     */
    public async getIncidents() {
        let userId: number;
        this.role = await this.session.getItem('role');
        if (this.role === UserRole.CLIENT) {
            userId = await this.session.getItem('userId');
            const url = 'incident/user-incident/' + userId;
            this.incidentProvider.getIncidentByIdUser(url).subscribe(
                (incidents) => {
                    if (!this.incidents && !this.incidentsTmp) {
                        this.incidents = [];
                        this.incidentsTmp = [];
                        this.positions = [];
                    }
                    this.incidents = this.incidents.concat(incidents.slice());
                    this.incidentsTmp = this.incidentsTmp.concat(this.incidentsTmp.slice());
                }
            );
        } else {
            const url = 'incident/';
            this.incidentProvider.getAllIncidentsWithDescription(url).subscribe(
                (incidents) => {
                    if (!this.incidents && !this.incidentsTmp) {
                        this.incidents = [];
                        this.incidentsTmp = [];
                        this.positions = [];
                    }
                    this.incidents = this.incidents.concat(incidents.slice());
                    this.incidentsTmp = this.incidentsTmp.concat(this.incidentsTmp.slice());
                }
            );
        }
    }

}
